.footer {
  padding: 42px 0;

  background-color: $color-black-russian;

  @include vp-1199 {
    padding: 45px 0;
  }
  @include vp-767 {
    padding: 50px 0;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__wrapper a:first-child {
  flex-shrink: 0;
  align-self: start;

  @include vp-767 {
    position: relative;
  }
}

.footer__wrapper a:hover,
.footer__wrapper a:active,
.footer__wrapper a:focus {
  opacity: 0.8;
}

.footer__social {
  flex-shrink: 0;
  align-self: flex-start;
  width: 110px;

  @include vp-767 {
    width: 148px;
  }
}

.footer__social ul {
  display: grid;
  justify-content: end;
  margin: 0;
  padding: 0;

  list-style: none;

  gap: 19px;
  grid-template-columns: repeat(auto-fit, 24px);
  @include vp-1199 {
    gap: 19px;
  }
  @include vp-767 {
    gap: 26px;
    grid-template-columns: repeat(auto-fit, 32px);
  }
}

.footer__social li {
  align-self: center;

  justify-self: end;

  & a {
    width: 100%;
  }
}

.footer__social li:last-child {
  margin-right: 0;
}
